.not_found {
    height: 800px;
    background-image: url('../../assets/images/not_found.jpg');
    background-size: cover;
    background-repeat: no-repeat;
  background-position: center, top;
}

.container {
    width: 589px;
    height: 800px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.error {
    font-family: "Cardo", serif;
    font-size: 150px; /* Adjust size as needed */
    font-weight: bold;
    color: #1E3A66; /* Blue text */
    text-shadow: 
      5px 5px 0 #FFC72C; /* Yellow offset shadow */
  }
.content {
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 41px;
}
.top {
    display: flex;
    flex-direction: column;
    gap: 16px;
}
.title {
  font-family: "Cardo", serif;
  font-weight: bold;
  font-size: 40px;
  color: var(--blue-color);
  margin: 0;
}
.description {
  color: var(--gray-color-1);
  margin: 0;
  line-height: 180%;
}
.btn {
    width: 220px;
    height: 65px;
    font-family: "Cardo", serif;
    font-size: 19px;
    background-color: var(--yellow-color);
    color: var(--blue-color);
}